/**
 * 
 */
export default {
  state: {
    /**
     * testrun
     * 
     */
    testrun: [],
    /**
     * testrun
     * 
     */
    testrunAll: []
  },
  mutations: {
    /**
     *  
     * @param {*}} state 
     * @param {*} payload TestRun
     */
    uploadTestRun(state, payload) {
      if (payload != null) {
        const index = state.testrun.findIndex(element => element.id === payload.id)
        if (index !== -1) {
          state.testrun[index] = payload
          console.log('TestRun ')
        } else {
          state.testrun.push(payload)
          console.log('TestRun ')
        }
      }
    },

    /**
     *  
     * @param {*}} state 
     * @param {*} payload TestRun
     */
    uploadTestRunAll(state, payload) {
      if (payload != null) {
        const index = state.testrunall.findIndex(element => element.id === payload.id)
        if (index !== -1) {
          state.testrunall[index] = payload
          console.log('TestRunAll ')
        } else {
          state.testrunall.push(payload)
          console.log('TestRunAll ')
        }
      }
    },

    /**
     *
     * @param {*} state
     * @param {*} payload
     */
    importTestRunCommon(state, payload) {
      if (payload != null) {
        const json = JSON.parse(payload)
        state.testrun = json
        console.log('testrun')
      }
    }
  },
  actions: {}
}
