const getAllMessage = () => {
  const messages = []
  for (let index = 0; index < 20; index++) {
    const single = {
      author: 'admin',
      header: '-' + index,
      reciver: 'Nate ford',
      sendtime: nowdate(),
      content: ',',
      isread: false
    }
    messages.push(single)
  }
  for (let index = 0; index < 3; index++) {
    const single = {
      author: '',
      header: '',
      reciver: 'Nate ford',
      sendtime: nowdate(),
      content: ',',
      isread: false
    }
    messages.push(single)
  }
  for (let index = 0; index < 2; index++) {
    const single = {
      author: '',
      header: '' + index,
      reciver: '',
      sendtime: nowdate(),
      content:
        'DOM element the toast will be created on. Note that this also changes the position of the toast from fixed to absolute. Meaning that the container should be fixed. Also note that this will override the defaultContainerElement if you specified it in your Buefy Constructor Options. See Constructor options for more details.',
      isread: true
    }
    messages.push(single)
  }

  return messages
}

