import { getAllMessage } from '../../scripts/common'
/**
 * 
 */
export default {
  state: {
    /**
     * UPDATE: cookie
     * NOTICE: cookieactiveusers.id
     */
    activeusers: {
      id: '',
      unickname: '',
      uemail: '',
      uname: '',
      upsd: '',
      ulastlogin: ''
    },
    /**
     * COM: message.vue/sidemessage.vue
     * UPDATE: 2021-02-23
     * NOTICE: 
     * NOTICE: 
     */
    allmessages: getAllMessage()
  },
  mutations: {
    /**
     * 
     * @param {*} state
     * @param {*} payload
     */
    updateUserInfo(state, payload) {
      // console.log(payload)
      if (payload != null) {
        if (state.activeusers.id === payload.resbody.id) {
          // 
          state.activeusers = payload.resbody
          console.log('Common ')
        } else {
          // 
          state.activeusers = payload.resbody
          console.log('Common ')
        }
      }
      // console.log(state.activeusers)
    }
  },
  actions: {}
}
