/**
 * 
 * @param {*} emun 
 */
const makeEemu = emun => {
  const all = []
  for (const key in emun) {
    all.push(emun[key])
  }
  return all
}

/**
 * guid
 */
const createGUID = () => {
  let guid = ''
  for (let i = 1; i <= 32; i++) {
    const n = Math.floor(Math.random() * 16.0).toString(16)
    guid += n
    if (i === 8 || i === 12 || i === 16 || i === 20) {
      guid += '-'
    }
  }
  return guid
}

