import axios from 'axios'
import qs from 'qs'
import superagent from 'superagent'

import { METHOD, BODYMODE } from '../internal'

/**
 * HTTP 
 * Cors
 * 
 * NOTICE: ,
 * @param {Object} stepinfo 
 */
const httpEntranceNoCors = stepinfo => {
  const method = stepinfo.request.method // 
  const url = stepinfo.request.url.raw // 
  const headers = returnHeaders(stepinfo.request.header) // headers
  let result
  let body
  switch (method) {
    case METHOD.POST:
      body = returnBody(stepinfo.request.body) // body
      result = postFuncNoCors(url, headers, body)
      break
    case METHOD.GET:
      result = getFuncNoCors(url, headers)
      break
  }
  return result
}

/**
 * CorsPOST
 * @param {String} url 
 * @param {Object} header 
 * @param {*} body 
 */
const postFuncNoCors = (url, header, body) => {
  return new Promise(function(resolve, reject) {
    superagent
      .post(url)
      .send(body)
      .set(header)
      .end(function(err, res) {
        if (err) return reject(err)
        resolve(res)
      })
  })
}

/**
 * CorsGET
 * @param {String} url 
 * @param {Object} header 
 */
const getFuncNoCors = (url, header) => {
  return new Promise(function(resolve, reject) {
    superagent
      .get(url)
      .withCredentials()
      .set(header)
      .end(function(err, res) {
        if (err) return reject(err)
        resolve(res)
      })
  })
}

/**
 * POST
 * @param {String} url 
 * @param {Object} header 
 * @param {*} body 
 */
const postFunc = (url, body) => {
  // 
  axios.defaults.baseURL = '/api'
  axios.defaults.withCredentials = true
  // 
  const bodyF = qs.stringify(body) // FormData
  return new Promise(function(resolve, reject) {
    axios
      .post(url, bodyF)
      .then(res => {
        console.log(res.data)
        resolve(res.data)
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * GET
 * @param {String} url 
 * @param {Object} params restfulparam
 */
const getFunc = (url, params) => {
  // 
  axios.defaults.baseURL = '/api'
  axios.defaults.withCredentials = true
  // 
  return new Promise(function(resolve, reject) {
    axios
      .get(url, {
        params: params
      })
      .then(res => {
        console.log(res.data)
        resolve(res.data)
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * header
 * 
 * @param {Array} headerarray
 * @returns {Object} headers header
 */
function returnHeaders(headerarray) {
  // headers
  const headers = {}
  headerarray.forEach(element => {
    if (element.select === true) {
      headers[element.key] = element.value
    }
  })
  return headers
}

/**
 * body form
 * body
 * @param {Array} bodyarray
 * @returns
 */
function returnBodyForm(bodyarray) {
  const body = {}
  // console.log(bodyarray)
  bodyarray.forEach(element => {
    if (element.select === true) {
      body[element.key] = element.value
    }
  })
  const bodyF = qs.stringify(body) // FormData
  return bodyF
}

/**
 * body object
 * body
 * @param {*} bodyarray
 * @returns
 */
function returnBodyObject(bodyarray) {
  const body = {}
  bodyarray.forEach(element => {
    if (element.select === true) {
      body[element.key] = element.value
    }
  })
  return body
}

/**
 * body
 * @param {Object} bodyObject stepinfo.body
 */
function returnBody(bodyObject) {
  let body // headers
  const bodymode = bodyObject.mode
  if (bodymode === BODYMODE.FORMDATA) {
    body = returnBodyForm(bodyObject.formbody)
  } else if (bodymode === BODYMODE.URLENCODED) {
    body = returnBodyForm(bodyObject.formbody)
  } else if (bodymode === BODYMODE.JSON) {
    body = returnBodyObject(bodyObject.formbody)
  } else {
    body = returnBodyObject(bodyObject.raw)
  }

  return body
}

export { httpEntranceNoCors }
export { postFunc, getFunc }
